	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Разгибание рук на блоке является изолирующее упражнение и направлено на проработку трицепса. Упражнение придает форму и делает трицепс рельефным. Может выполняться в нескольких вариантах (с V-образной рукоятью, с канатной рукоятью, с прямой рукоятью).</p>\n<h3>Порядок выполнения:</h3>\n<ol>\n<li>Нагрузите тросовый тренажер необходимым весом. Возьмитесь за рукоять, присоединенную к тросу верхнего блока хватом сверху, немного уже ширины плеч.</li>\n<li>Исходное положение – потяните рукоять тренажера вниз и прижмите локти к туловищу. Слегка наклонитесь вперед и немного согните колени. Руки согнуты в локтях, предплечья параллельны полу и обращены к тренажеру, а часть рук от локтя и выше должна быть перпендикулярна полу.</li>\n<li>На выдохе: держа неподвижной часть руки от плеча до локтя, полностью разогните руки опуская рукоять вниз, касаясь бедер. Рука от плеча до локтя неподвижна. Задержитесь на секунду в этом положении.</li>\n<li>На вдохе: медленно верните рукоять исходное положение.</li>\n<li>Сделайте необходимое число повторов.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n<li>Наклоняйтесь вперед совсем немного – иначе вы будете вополнять упражнение не за счет напряжения мышц, а за счет веса своего тела.</li>\n<li>Следите за локтями! На протяжении всего упражнения они должны оставаться неподвижными.</li>\n<li>Во время разгибания рук на вертикальном блоке спина должна быть прямой, а грудь развернутой. Допускается для устойчивости одну ногу выносить вперед.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/DG_qts6PodE\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/razgibania-ruk-na-bloke-stoya","efficiency":"7","title":"Разгибание рук на блоке стоя","image":"/static/4-01-0.jpg","image_2":"","image_3":"","image_4":"","filterby":"triceps","date":"April 12, 2017","injury":"4","bodypart":"Трицепс","filtertype":"isolated","type":"Изолирующее","filterinventory":"blocktrenajer","inventory":"Блоки","filterplace":null}}},"pathContext":{}}

